import { FETCH_SHOPS_SUCCESS } from "../actions/shopActions";

const initState = {
  products: [],
};

const shopsReducer = (state = initState, action) => {
  if (action.type === FETCH_SHOPS_SUCCESS) {
    return {
      ...state,
      shops: action.payload,
    };
  }

  return state;
};

export default shopsReducer;
