import axios from "axios";

export const FETCH_SHOPS_SUCCESS = "FETCH_SHOPS_SUCCESS";

const fetchShopsSuccess = (shops) => ({
  type: FETCH_SHOPS_SUCCESS,
  payload: shops,
});

export const fetchShops = () => {
  return (dispatch) => {
    axios({
      url: "http://147.182.198.134/graphql",
      method: "post",
      data: {
        query: `query{
  shops {
    brandsCarried
    city
    description
    id
    latitude
    longitude
    name
    phone
    postalCode
    state
    street
    url
  }
}`,
      },
    })
      .then((result) => {
        return result;
      })
      .then((json) => {
        console.log(json.data.data.shops);
        dispatch(fetchShopsSuccess(json.data.data.shops));
      });
  };
};

