import axios from "axios";

export const FETCH_PRODUCTS_SUCCESS = "FETCH_PRODUCTS_SUCCESS";

const fetchProductsSuccess = (products) => ({
  type: FETCH_PRODUCTS_SUCCESS,
  payload: products,
});

export const fetchProducts = () => {
  return (dispatch) => {
    axios({
      url: "http://147.182.198.134/graphql",
      method: "post",
      data: {
        query: `query{
  bikes {
    imageSet {
      url
    }
    color
    details
    id
    images
    manufacturer
    name
    price
    shopRelation{
      brandsCarried
      city
      description
      id
      latitude
      longitude
      name
      phone
      postalCode
      state
      street
      url
    }
    shop
    size
    specs
  }
}`,
      },
    })
      .then((result) => {
        return result;
      })
      .then((json) => {
//        console.log(json.data.data.bikes);
        dispatch(fetchProductsSuccess(json.data.data.bikes));
      });
  };
};

